import * as React from "react"
import { PageProps, graphql } from "gatsby"

import * as styles from "../styles/markdown.module.css"
import Layout from "../components/layout"
import Seo from "../components/seo"

type MarkdownPageProps = PageProps<DataProps>;

const MarkdownPageTemplate: React.FC<MarkdownPageProps> = ({ data, path }) => {
  const {
    title,
  } = data.markdownRemark.frontmatter;
  const body = data.markdownRemark.html;

  return <Layout path={path}>
    <Seo title={title} />
    <div className={styles.standard} dangerouslySetInnerHTML={{ __html: body }} />
  </Layout>;
}

export default MarkdownPageTemplate

export const query = graphql`
    query($slug: String!) {
        markdownRemark(fields: { slug: { eq: $slug } }) {
            html
            frontmatter {
                title,
                className
            }
        }
    }
`;
interface DataProps {
  markdownRemark: {
    html: string;
    frontmatter: {
      title: string;
    };
  };
}
